export default {
  data() {
    return {
      valid: false,
      rules: {
        required: (value) =>
          !!value || this.$t('errorMessages.form.fieldRequired'),
        confirmPassword: (value, v2) =>
          value === v2 || this.$t('errorMessages.form.confirmPasswordNotMatch'),
        isEmail: (value) =>
          // eslint-disable-next-line
          /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value) ||
          this.$t('errorMessages.form.addValidEmail'),
        passwordMinLength: (value) =>
          (value && value.length >= 8) ||
          this.$t('errorMessages.form.passwordLength'),
        isImage: (value) => {
          if (value && value.type) {
            return (
              value.type.startsWith('image') ||
              this.$t('errorMessages.form.plzUploadImage')
            )
          } else {
            return true
          }
        },
        imageSize: (value) => {
          if (value && value.size) {
            return (
              value.size < 2048000 || this.$t('errorMessages.form.imageSize')
            )
          } else {
            return true
          }
        }
      }
    }
  },
  methods: {
    validate() {
      this.$refs.form.validate()
    }
  }
}
