<template>
  <v-form ref="replyForm" v-model="valid" @submit.prevent="add">
    <v-textarea
      v-model="text"
      :label="$t('replies.form.replyLabel')"
      single-line
      outlined
      rounded
      :rules="[rules.required]"
    />
    <v-text-field
      v-if="!$auth.loggedIn"
      v-model="name"
      outlined
      rounded
      :label="$t('replies.form.nameLabel')"
      :rules="[rules.required]"
    />
    <v-text-field
      v-if="!$auth.loggedIn"
      v-model="email"
      outlined
      rounded
      :label="$t('replies.form.emailLabel')"
      :rules="[rules.required, rules.isEmail]"
    />
    <v-btn :loading="loading" type="submit" rounded class="primary white--text">
      {{ $t('replies.form.button.text') }}
    </v-btn>
    <v-btn rounded class="ml-2" color="error" outlined @click="removeForm">
      {{ $t('replies.form.closeButton.text') }}
    </v-btn>
  </v-form>
</template>

<script>
import { mapActions } from 'vuex'
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  props: {
    comment: {
      type: Object,
      required: true
    },
    showForm: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      text: '',
      name: '',
      email: '',
      loading: false
    }
  },
  methods: {
    ...mapActions('reply', ['addReply']),
    async add() {
      this.$refs.replyForm.validate()
      if (this.valid) {
        let rp = {}
        if (this.$auth.loggedIn) {
          rp = {
            text: this.text,
            comment: this.comment._id,
            user: this.$auth.user._id,
            slug: this.comment.post.slug
          }
        } else {
          rp = {
            text: this.text,
            comment: this.comment._id,
            name: this.name,
            email: this.email,
            slug: this.comment.post.slug
          }
        }
        const payload = rp
        try {
          this.loading = true
          await this.addReply({ reply: payload, axios: this.$axios })
          this.$refs.replyForm.reset()
          this.loading = false
        } catch (e) {
          this.loading = false
        }
      }
    },
    removeForm() {
      const form = document.querySelector('.reply-comment-' + this.comment._id)
      form.style.display = 'none'
    }
  }
}
</script>

<style lang="scss" scoped></style>
